.page-header {
  margin-bottom: 50px;
}

.crop {
  width: 100%;
  height: 200px;
  overflow: hidden;
  position: relative;
}

.crop img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
}

.right.attached.button {
  position: fixed;
  top: 63px;
  z-index: 500;
  text-transform: uppercase;
  line-height: 1;
  font-weight: 700;
}

.ui.pagination.menu {
  margin: 30px 0 40px;
}

.image.content .description {
  position: relative
}

.description .modal-time {
  position: absolute;
  bottom: 0;
  right: 0;
  color: #5A5A5A;
}

.ui.cards>.ui.card>.content>.slim.header {
  font-weight: 500;
}